import {
  S3Client,
  CreateMultipartUploadCommand,
  UploadPartCommand,
  CompleteMultipartUploadCommand,
  AbortMultipartUploadCommand,
  PutBucketCorsCommandInput,
  DeleteObjectCommand,
  PutBucketCorsCommand,
  PutObjectCommand, // Added for deleteFile
} from "@aws-sdk/client-s3";
import { createHash } from "crypto";

// Environment variables
const ACCOUNT_ID = process.env.NEXT_PUBLIC_R2_ACCOUNT_ID;
const S3_BUCKET = process.env.NEXT_PUBLIC_AWS_S3_PUBLIC_BUCKET;
const ACCESS_KEY = process.env.NEXT_PUBLIC_AWS_ACCESS_KEY_ID;
const SECRET_KEY = process.env.NEXT_PUBLIC_AWS_SECRET_ACCESS_KEY;

// Validate environment variables
if (!ACCOUNT_ID || !S3_BUCKET || !ACCESS_KEY || !SECRET_KEY) {
  throw new Error(
    "Missing required environment variables for R2 configuration"
  );
}
const input = {
  // PutBucketCorsRequest
  Bucket: S3_BUCKET, // required
  CORSConfiguration: {
    CORSRules: [
      {
        AllowedHeaders: ["*"],
        AllowedMethods: ["PUT", "POST", "HEAD", "GET"],
        AllowedOrigins: ["http://localhost:3000"],
        ExposeHeaders: [],
        MaxAgeSeconds: 3000,
      },
    ],
  },
};
// Hash the secret access key
const hashedSecretKey =  createHash("sha256").update(SECRET_KEY).digest("hex");

// Configure S3 client for Cloudflare R2
const s3Client = new S3Client({
  endpoint: `https://${ACCOUNT_ID}.r2.cloudflarestorage.com`,
  region: "auto", // Cloudflare R2 doesn't use regions, but required by SDK
  credentials: {
    accessKeyId: ACCESS_KEY,
    secretAccessKey: SECRET_KEY// hashedSecretKey,
  },
  signatureVersion: "v4",
});
console.log(
  "s3Client  Data",
  s3Client.config.endpoint,
  s3Client.config.credentials
);

const CHUNK_SIZE = 5 * 1024 * 1024; // 5MB per chunk

// Function to create file chunks
const createChunks = (file: File): Blob[] => {
  if (!(file instanceof File)) {
    throw new Error("Invalid file object provided");
  }
  console.log("File size:", file.size, "Chunk size:", CHUNK_SIZE);
  const chunks: Blob[] = [];
  let currentByte = 0;

  while (currentByte < file.size) {
    const chunk = file.slice(currentByte, currentByte + CHUNK_SIZE);
    chunks.push(chunk);
    currentByte += CHUNK_SIZE;
  }
  console.log("Total chunks created:", chunks.length);
  return chunks;
};

export const uploadFileInChunks = async (
  file: File,
  fileName: string,
  fileType: string,
  uniqueFileKey: string,
  setProgress: (progress: Record<string, number>) => void,
  folderName: string = new Date(Date.now()).toLocaleDateString("en-GB")
): Promise<string> => {
  const chunks = createChunks(file);
  console.log("Starting upload to bucket:", S3_BUCKET);
  const uploadId = await initiateMultipartUpload(
    fileName,
    fileType,
    folderName,
  file
  );
  const uploadPromises = chunks.map((chunk, index) =>
    uploadChunk(uploadId, chunk, index + 1, fileName, folderName).then(
      (part) => {
        const progress = Math.round(((index + 1) / chunks.length) * 100);
        setProgress({ [uniqueFileKey]: progress });
        return part;
      }
    )
  );

  try {
    const parts = await Promise.all(uploadPromises);
    const result = await completeMultipartUpload(
      uploadId,
      parts,
      fileName,
      folderName
    );
    setProgress({ [uniqueFileKey]: 100 });
    return result;
  } catch (error) {
    console.error("Upload failed:", error);
    await abortMultipartUpload(uploadId, fileName, folderName);
    throw error;
  }
};

// Initiate multipart upload
const initiateMultipartUpload = async (
  fileName: string,
  fileType: string,
  folderName: string,
  file: any
): Promise<string> => {
  const chunkArrayBuffer = await file.arrayBuffer();
  const chunkUint8Array = new Uint8Array(chunkArrayBuffer);
  // const commandNew = new PutObjectCommand({
  //   Bucket: S3_BUCKET,
  //   Key: `${folderName}${fileName}`,
  //   Body: chunkUint8Array,
  //   // CORSConfiguration: {
  //   //   CORSRules: [
  //   //     {
  //   //       AllowedHeaders: ["content-type"],
  //   //       AllowedMethods: ["PUT", "POST", "HEAD", "GET"],
  //   //       AllowedOrigins: ["*"],
  //   //       ExposeHeaders: [],
  //   //       MaxAgeSeconds: 3000,
  //   //     },
  //   //   ],
  //   // },
  // });
  const command = new CreateMultipartUploadCommand({
    Bucket: S3_BUCKET,
    Key: `${folderName}${fileName}`,
    ContentType: fileType,
    ACL:"public-read",
    ...input.CORSConfiguration,
  });
  console.log("command", command);
  try {
    const response = await s3Client.send(command);
    if (!response.UploadId) {
      throw new Error('UploadId not returned from R2');
    }
    console.log("Upload initiated with ID:", response);
    return response.UploadId;
  } catch (error) {
    console.error("Failed to initiate multipart upload:", error);
    throw error;
  }
};

// Upload a single chunk
const uploadChunk = async (
  uploadId: string,
  chunk: Blob,
  partNumber: number,
  fileName: string,
  folderName: string
): Promise<{ ETag: string; PartNumber: number }> => {
  const chunkArrayBuffer = await chunk.arrayBuffer();
  const chunkUint8Array = new Uint8Array(chunkArrayBuffer);

  const command = new UploadPartCommand({
    Bucket: S3_BUCKET,
    Key: `${folderName}${fileName}`,
    PartNumber: partNumber,
    UploadId: uploadId,
    Body: chunkUint8Array,
    ...input.CORSConfiguration,
  });

  try {
    const response = await s3Client.send(command);
    if (!response.ETag) {
      throw new Error(`No ETag returned for part ${partNumber}`);
    }
    console.log(`Uploaded part ${partNumber}:`, response.ETag);
    return {
      ETag: response.ETag,
      PartNumber: partNumber,
    };
  } catch (error) {
    console.error(`Failed to upload part ${partNumber}:`, error);
    throw error;
  }
};

// Complete multipart upload
const completeMultipartUpload = async (
  uploadId: string,
  parts: { ETag: string; PartNumber: number }[],
  fileName: string,
  folderName: string
): Promise<string> => {
  const sortedParts = parts.sort((a, b) => a.PartNumber - b.PartNumber);
  const command = new CompleteMultipartUploadCommand({
    Bucket: S3_BUCKET,
    Key: `${folderName}${fileName}`,
    UploadId: uploadId,
    MultipartUpload: { Parts: sortedParts },
    ...input.CORSConfiguration,
  });

  try {
    const response = await s3Client.send(command);
    if (!response.Key) {
      throw new Error("No Key returned after completing upload");
    }
    console.log("Upload completed successfully. Key:", response.Key);
    return response.Key;
  } catch (error) {
    console.error("Failed to complete multipart upload:", error);
    throw error;
  }
};

// Abort multipart upload
const abortMultipartUpload = async (
  uploadId: string,
  fileName: string,
  folderName: string
): Promise<void> => {
  const command = new AbortMultipartUploadCommand({
    Bucket: S3_BUCKET,
    Key: `${folderName}${fileName}`,
    UploadId: uploadId,
    ...input.CORSConfiguration,
  });

  try {
    await s3Client.send(command);
    console.log("Multipart upload aborted successfully:", uploadId);
  } catch (error) {
    console.error("Failed to abort multipart upload:", error);
    throw error;
  }
};

// Delete file
export const deleteFile = async (
  filepath: string,
  onProgress?: (progress: any) => void
): Promise<{ ETag?: string }> => {
  const command = new DeleteObjectCommand({
    Bucket: S3_BUCKET,
    Key: filepath,
    ...input.CORSConfiguration,
  });

  try {
    const response = await s3Client.send(command);
    console.log("File deleted successfully:", filepath);
    // Optionally call onProgress if provided
    if (onProgress) {
      onProgress({ loaded: 1, total: 1 });
    }
    return { ETag: response.DeleteMarker?.toString() };
  } catch (error) {
    console.error("Failed to delete file:", error);
    throw error;
  }
};
